@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
body{margin:0;width:100vw;overflow-x:hidden}#home-container{margin:0 auto;width:100vw;min-height:12rem;background-image:url(../../static/media/gear-2291916_1920.0aa85daa.jpg);background-repeat:no-repeat;background-size:cover}h1{font-family:'Righteous', cursive}h3{font-size:1rem;color:white;text-shadow:black 0.1rem 0.1rem 0.6rem}input,button{padding:0.3rem;margin:0.5rem 0;background-color:#e6dbdb}.App{margin:0 auto;text-align:center;background-color:#090422}.App-logo{width:2.8rem;pointer-events:none;margin:0.2rem;transform:rotate(-44deg)}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 11s linear;animation:App-logo-spin infinite 11s linear}}.App-header{background-color:#435e4c;padding:.5rem;height:9vh;border:3px solid #585151;display:flex;flex-direction:row;color:#0f1447;text-shadow:white 0.1rem 0.1rem 0.6rem;background-image:url(../../static/media/gear-2291916_1920.0aa85daa.jpg);background-size:cover;background-repeat:no-repeat}.App-header h1{margin-left:0.5rem;margin-bottom:1rem}.App-header span{font-size:1rem;color:white;text-shadow:black 0.1rem 0.1rem 0.6rem}.App-link{color:#61dafb}#products-container{background-color:#090422}.result-search{background-color:#090422;color:white;width:10rem;text-align:center;margin:0 auto;list-style:none;padding-bottom:2rem;text-align:left}.ex{color:white;border:white 2px solid;position:relative;top:1rem;left:8rem;padding:0.1rem;border-radius:4px}#contact-container{height:11rem;color:white;font-weight:bold;text-shadow:2px 2px 3px #7a7a7a;background-image:url(../../static/media/bike-1836934_1920.d6656875.jpg);background-size:cover}iframe{width:100%;margin:0}.carousel-section{border:#4c4c57 solid 5px}.carousel-section h3{color:white}@media only screen and (min-width: 732px){.carousel-section{width:70%;margin:0 auto}#contact-container{width:70%;margin:0 auto}}

.navbar{max-width:100%;margin:0 auto;padding:0.2rem;display:flex;flex-direction:column;background-color:#090422}.navbar .links{max-width:100%;display:flex;justify-content:space-around;flex-direction:column;align-items:center}.navbar a{height:1.4rem;margin:0.35rem 3rem;padding:0.1rem;cursor:pointer;color:#ebe3e3;letter-spacing:0.2rem;background-color:rgba(76,82,95,0.5);border:#8d90a1 2px solid;border-radius:6px;font-family:'Righteous', cursive}.navbar .hamburger .line{width:30px;height:5px;background-color:#d4cccc;display:block;margin:8px auto;transition:all 0.3s ease-in-out}.navbar .hamburger:hover{cursor:pointer}.navbar #hamburger-navbar{position:relative}.navbar #hamburger-navbar.is-active{transition:all 0.3s ease-in-out;transition-delay:0.6s;transform:rotate(45deg)}.navbar #hamburger-navbar.is-active .line:nth-child(2){width:0px}.navbar #hamburger-navbar.is-active .line:nth-child(1),.navbar #hamburger-navbar.is-active .line:nth-child(3){transition-delay:0.3s}.navbar #hamburger-navbar.is-active .line:nth-child(1){transform:translateY(13px)}.navbar #hamburger-navbar.is-active .line:nth-child(3){transform:translateY(-13px) rotate(90deg)}@media only screen and (min-width: 732px){.navbar{flex-direction:row;justify-content:center;align-content:center}.navbar .links{flex-direction:row;justify-content:space-around;padding:0.35rem}}

